<template>
<div class="blog-list-container" ref="mainContainer" v-loading="isLoading">
  <ul>
    <li v-for="item in data.rows" :key="item.id">
      <div class="thumb" v-if="item.thumb">
        <RouterLink :to="{
          name:'BlogDetail',
            params: {
              id: item.id
            }
        }">
          <img v-lazy="item.thumb" :alt="item.title" :title="item.title" />
        </RouterLink>
      </div>
      <div class="main">
        <RouterLink :to="{
          name:'BlogDetail',
          params: {
            id: item.id
          }
        }">
          <h2>{{ item.title }}</h2>
        </RouterLink>
        <div class="aside">
          <span>日期：{{ item.createDate }}</span>
          <span>浏览：{{ item.scanNumber }}</span>
          <span>评论：{{ item.commentNumber }}</span>
          <RouterLink :to="{
            name:'CategoryBlog',
            params: {
              categoryId: item.category.id
            }
          }">{{ item.category.name }}</RouterLink>
        </div>
        <div class="desc">
          {{item.description}}
        </div>
      </div>
    </li>
  </ul>
  <Pager v-if="data.total"
         :total="data.total"
         :limit="routeInfo.limit"
         :current="routeInfo.page"
         :visible-number="10"
         @pageChange="handlePageChange"/>
</div>
</template>

<script>
import Pager from "@/components/Pager";
import fetchData from "@/mixins/fetchData";
import { getBlogs } from "@/api/blog";
import mainScroll from "@/mixins/mainScroll";

export default {
  name: "BlogList",
  mixins: [fetchData({}), mainScroll("mainContainer")],
  components: {
    Pager
  },
  watch: {
    async $route(){
      this.isLoading = true;
      //滚动高度为0
      this.$refs.mainContainer.scrollTop = 0;
      this.data = await this.fetchData();
      this.isLoading = false;
    }
  },
  methods: {
    async fetchData(){
      return await getBlogs();
    },
    handlePageChange(newPage){
      const query = {
        page: newPage,
        limit: this.routeInfo.limit,
      }
      //跳转到当前的分类id，当前的页容量 newPage的页码
      if (this.routeInfo.categoryId === -1){
        //当前没有分类
        //   /article/cate/-1?page=${newPage}&limit=${this.routeInfo.limit}
        this.$router.push({
          name: "Blog",
          query
        })
      }else{
        //   /article/cate/${this.routeInfo.categoryId}/page=${newPage}&limit=${this.routeInfo.limit}
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: this.routeInfo.categoryId
          }
        })
      }
    },
  },
  beforeUpdate() {
    this.data.rows.sort((a, b) => parseInt(b.createDate) - parseInt(a.createDate));
  },
  computed: {
    //获取路由信息
    routeInfo(){
      const categoryId = +this.$route.params.categoryId || -1;//加号用来将读取到的字符串转换为数字
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 20;
      return {
        categoryId,
        page,
        limit
      }
    }
  }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
@import "~@/styles/mixin.less";
.blog-list-container {
  line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: 10ms;
  .scroll();
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 2px solid @gray;
  .thumb {
    flex: 0 0 auto;
    margin-right: 15px;
    img {
      display: block;
      max-width: 200px;
      border-radius: 5px;
    }
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .aside {
    font-size: 12px;
    color: @dark;
    span {
      margin-right: 15px;
    }
  }
  .desc {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>